.m-celltitle {
  padding: .15rem @body-padding-vertical .1rem;
  font-size: @cell-title-fontsize; /* px */
  text-align: left;
  color: @cell-title-color;
  position: relative;
  &:after {
    .bottom-line(@line-color);
  }
}

.m-cell {
  background-color: #FFF;
  margin-bottom: .2rem;
  position: relative;
  &:after {
    .bottom-line(@line-high-color);
  }
  a.u-cellitem,
  label.u-cellitem {
    .tap-color(#FFF, .96);
  }
}

.u-cellitem {
  display: flex;
  position: relative;
  padding-left: @body-padding-vertical;
  overflow: hidden;
  &:not(:last-child):after {
    margin-left: @body-padding-vertical;
    .bottom-line(@line-color);
  }
}

.cell-left {
  color: @cell-left-color;
  font-size: @cell-left-fontsize; /* px */
  white-space: nowrap;
  display: flex;
  align-items: center;
}

.cell-right {
  flex: 1;
  width: 100%;
  min-height: @cell-height;
  color: @cell-right-color;
  text-align: right;
  font-size: @cell-right-fontsize; /* px */
  padding-right: @body-padding-vertical;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  input[type="datetime-local"],
  input[type="date"],
  input[type="time"] {
    line-height: @cell-height;
  }
  input[type="radio"],
  input[type="checkbox"]:not(.checkbox-ios) {
    appearance: none;
    position: absolute;
    left: -9999em;
    & + .cell-radio-ico:after,
    & + .cell-checkbox-ico:after {
      display: none;
      font-size: .4rem; /* px */
    }
    &:checked {
      & + .cell-radio-ico:after {
        color: #4CD864;
        content: '\e600';
        display: inline-block;
        font-family: @iconfont-inlay;
      }
      & + .cell-checkbox-ico:after {
        color: #4CD864;
        content: '\e601';
        display: inline-block;
        font-family: @iconfont-inlay;
      }
    }
  }
}

.cell-textarea {
  width: 100%;
  border: none;
  display: block; /* for old android */
  height: 1.5rem;
  padding: .2rem 0;
}

.cell-select {
  flex: 1;
  line-height: @cell-height;
  border: none;
  display: block;
  color: @cell-select-color;
  font-size: @cell-select-fontsize; /* px */
  margin-left: -.08rem; /* select */
}

.cell-input {
  flex: 1;
  height: @cell-height;
  border: none;
  line-height: 1.25;
  font-size: @cell-input-fontsize; /* px */
  background: transparent;
  color: @cell-input-color;
  display: flex;
  justify-content: flex-start;
  text-align: left; /* fuck UC */
}

.cell-icon {
  display: block;
  &:before, &:after {
    color: #A6A5A5;
    font-size: @cell-icon-fontsize !important; /* px */
    margin-right: .1rem;
  }
  img {
    width: @cell-icon-fontsize;
    margin-right: .1rem;
  }
}

.cell-arrow:after {
  margin-left: .05rem;
  display: block;
  font-family: @iconfont-inlay;
  font-size: @cell-arrow-fontsize;
  color: @cell-arrow-color;
  content: '\e608';
}

.checkbox-ios {
  appearance: none;
  position: relative;
  width: 52px; /* no */
  height: 32px; /* no */
  border: 1px solid #DFDFDF; /* no */
  border-radius: 16px; /* no */
  background-color: #DFDFDF;
  &:before, &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 30px; /* no */
    border-radius: 15px; /* no */
    transition: transform .3s;
  }
  &:before {
    width: 50px; /* no */
    background-color: #FDFDFD;
  }
  &:after {
    width: 30px; /* no */
    background-color: #FFF;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .4); /* no */
  }
  &:checked {
    border-color: #4CD864;
    background-color: #4CD864;
    &:before {
      transform: scale(0);
    }
    &:after {
      transform: translateX(20px); /* no */
    }
  }
}