.m-button {
  padding: 0 0.24rem;
}
.btn {
  text-align: center;
  position: relative;
  border: none;
  line-height: 0.7rem;
  font-size: 0.26rem;
  /* px */
  display: inline-block;
  padding: 0 .2rem;
  border-radius: 3px;
  /* no */
}
.btn-block {
  text-align: center;
  position: relative;
  border: none;
  width: 100%;
  display: block;
  font-size: 0.36rem;
  /* px */
  line-height: 1rem;
  margin-top: 0.5rem;
  border-radius: 3px;
  /* no */
}
.btn-primary {
  background-color: #04BE02;
  color: #FFF;
}
.btn-primary:active {
  background-color: #04ab02;
}
.btn-danger {
  background-color: #EF4F4F;
  color: #FFF;
}
.btn-danger:active {
  background-color: #d74747;
}
.btn-warning {
  background-color: #FFB400;
  color: #FFF;
}
.btn-warning:active {
  background-color: #e6a200;
}
.btn-disabled {
  background-color: #CCC;
  color: #F0F0F0;
}
.btn-disabled:active {
  background-color: #cccccc;
}
.btn-hollow {
  background-color: #F7F7F7;
  color: #454545;
}
.btn-hollow:active {
  background-color: #dedede;
}
.btn-hollow:after {
  content: ' ';
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(0.5);
  transform-origin: 0 0;
  border: 1px solid #B2B2B2;
  border-radius: 6px;
}
input.btn-hollow {
  border: 1px solid #B2B2B2;
}
@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
@keyframes bounceInDown {
  from,
  60%,
  75%,
  90%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  0% {
    opacity: 0;
    transform: translate3d(0, -3000px, 0);
  }
  60% {
    opacity: 1;
    transform: translate3d(0, 25px, 0);
  }
  75% {
    transform: translate3d(0, -10px, 0);
  }
  90% {
    transform: translate3d(0, 5px, 0);
  }
  to {
    transform: none;
  }
}
.m-confirm {
  position: fixed;
  z-index: 999;
  width: 85%;
  top: 30%;
  left: 50%;
  background-color: #FAFAFA;
  border-radius: 3px;
  /* no */
  margin-left: -42.5%;
  animation: bounceInDown .3s ease forwards;
}
.m-alert {
  animation: zoomIn .25s ease forwards;
}
.m-alert .confirm-bd {
  text-align: center;
}
.m-alert .confirm-bd {
  padding: 20px 20px 0 20px;
  /* no */
}
.confirm-hd {
  text-align: left;
  padding: 15px 20px 10px;
  /* no */
}
.confirm-hd .confirm-title {
  font-weight: 400;
  font-size: 17px;
  /* no */
}
.confirm-bd {
  text-align: left;
  padding: 0 20px;
  /* no */
  font-size: 15px;
  /* no */
  color: #888;
  line-height: 20px;
  /* no */
}
.confirm-ft {
  position: relative;
  line-height: 42px;
  /* no */
  margin-top: 15px;
  /* no */
  font-size: 17px;
  /* no */
  overflow: hidden;
  display: flex;
  text-align: center;
}
.confirm-ft:after {
  content: '';
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  height: 1px;
  /* no */
  width: 100%;
  border-top: 1px solid #D9D9D9;
  /* no */
  transform: scaleY(0.5);
  transform-origin: 0 0;
}
.confirm-ft > a {
  word-wrap: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  text-align: center;
  position: relative;
  display: block;
  width: 1%;
  flex: 1;
  padding: 0 2px;
}
.confirm-ft > a:not(:last-child):after {
  content: '';
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
  height: 100%;
  width: 1px;
  /* no */
  border-right: 1px solid #D9D9D9;
  /* no */
  transform: scaleX(0.5);
  transform-origin: 100% 0;
}
.confirm-ft > a.confirm-btn.default {
  color: #353535;
}
.confirm-ft > a.confirm-btn.primary {
  color: #0BB20C;
}
@media screen and (min-width: 768px) {
  .m-confirm {
    width: 40%;
    margin-left: -20%;
  }
}
