.m-celltitle {
  padding: 0.15rem 0.24rem 0.1rem;
  font-size: 0.3rem;
  /* px */
  text-align: left;
  color: #888;
  position: relative;
}
.m-celltitle:after {
  content: '';
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  /* no */
  width: 100%;
  border-bottom: 1px solid #D9D9D9;
  /* no */
  transform: scaleY(0.5);
  transform-origin: 0 100%;
}
.m-cell {
  background-color: #FFF;
  margin-bottom: .2rem;
  position: relative;
}
.m-cell:after {
  content: '';
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  /* no */
  width: 100%;
  border-bottom: 1px solid #B2B2B2;
  /* no */
  transform: scaleY(0.5);
  transform-origin: 0 100%;
}
.m-cell a.u-cellitem,
.m-cell label.u-cellitem {
  background-color: #FFF;
}
.m-cell a.u-cellitem:active,
.m-cell label.u-cellitem:active {
  background-color: #f5f5f5;
}
.u-cellitem {
  display: flex;
  position: relative;
  padding-left: 0.24rem;
  overflow: hidden;
}
.u-cellitem:not(:last-child):after {
  margin-left: 0.24rem;
  content: '';
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  /* no */
  width: 100%;
  border-bottom: 1px solid #D9D9D9;
  /* no */
  transform: scaleY(0.5);
  transform-origin: 0 100%;
}
.cell-left {
  color: #333;
  font-size: 0.3rem;
  /* px */
  white-space: nowrap;
  display: flex;
  align-items: center;
}
.cell-right {
  flex: 1;
  width: 100%;
  min-height: 1rem;
  color: #525252;
  text-align: right;
  font-size: 0.28rem;
  /* px */
  padding-right: 0.24rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.cell-right input[type="datetime-local"],
.cell-right input[type="date"],
.cell-right input[type="time"] {
  line-height: 1rem;
}
.cell-right input[type="radio"],
.cell-right input[type="checkbox"]:not(.checkbox-ios) {
  appearance: none;
  position: absolute;
  left: -9999em;
}
.cell-right input[type="radio"] + .cell-radio-ico:after,
.cell-right input[type="checkbox"]:not(.checkbox-ios) + .cell-radio-ico:after,
.cell-right input[type="radio"] + .cell-checkbox-ico:after,
.cell-right input[type="checkbox"]:not(.checkbox-ios) + .cell-checkbox-ico:after {
  display: none;
  font-size: .4rem;
  /* px */
}
.cell-right input[type="radio"]:checked + .cell-radio-ico:after,
.cell-right input[type="checkbox"]:not(.checkbox-ios):checked + .cell-radio-ico:after {
  color: #4CD864;
  content: '\e600';
  display: inline-block;
  font-family: 'YDUI-INLAY';
}
.cell-right input[type="radio"]:checked + .cell-checkbox-ico:after,
.cell-right input[type="checkbox"]:not(.checkbox-ios):checked + .cell-checkbox-ico:after {
  color: #4CD864;
  content: '\e601';
  display: inline-block;
  font-family: 'YDUI-INLAY';
}
.cell-textarea {
  width: 100%;
  border: none;
  display: block;
  /* for old android */
  height: 1.5rem;
  padding: .2rem 0;
}
.cell-select {
  flex: 1;
  line-height: 1rem;
  border: none;
  display: block;
  color: #A9A9A9;
  font-size: 0.3rem;
  /* px */
  margin-left: -0.08rem;
  /* select */
}
.cell-input {
  flex: 1;
  height: 1rem;
  border: none;
  line-height: 1.25;
  font-size: 0.3rem;
  /* px */
  background: transparent;
  color: #555;
  display: flex;
  justify-content: flex-start;
  text-align: left;
  /* fuck UC */
}
.cell-icon {
  display: block;
}
.cell-icon:before,
.cell-icon:after {
  color: #A6A5A5;
  font-size: 0.42rem !important;
  /* px */
  margin-right: .1rem;
}
.cell-icon img {
  width: 0.42rem;
  margin-right: .1rem;
}
.cell-arrow:after {
  margin-left: .05rem;
  display: block;
  font-family: 'YDUI-INLAY';
  font-size: 0.34rem;
  color: #C9C9C9;
  content: '\e608';
}
.checkbox-ios {
  appearance: none;
  position: relative;
  width: 52px;
  /* no */
  height: 32px;
  /* no */
  border: 1px solid #DFDFDF;
  /* no */
  border-radius: 16px;
  /* no */
  background-color: #DFDFDF;
}
.checkbox-ios:before,
.checkbox-ios:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 30px;
  /* no */
  border-radius: 15px;
  /* no */
  transition: transform .3s;
}
.checkbox-ios:before {
  width: 50px;
  /* no */
  background-color: #FDFDFD;
}
.checkbox-ios:after {
  width: 30px;
