  position: absolute;
  left: -1000em;
}
.u-checkbox > input:checked + label:before {
  color: #FF5E53;
}
.u-checkbox > input:checked + label.checkbox-square:before {
  content: '\e605';
}
.u-checkbox > input:checked + label.checkbox-circle:before {
  content: '\e601';
}
.u-checkbox > label {
  color: #555;
