}
.u-checkbox > label:before {
  font-family: 'YDUI-INLAY';
  margin-right: .06rem;
  font-size: .4rem;
  /* px */
  color: #B7B7B7;
  position: relative;
  z-index: 0;
  top: 1px;
  transition: all .28s;
}
.u-checkbox > label.checkbox-square:before {
